
.card-frame{background:blue;-moz-column-width:200px;column-width:200px;-moz-column-gap:20px;column-gap:20px;width:100%
}
.page-splitter{width:100%;border-bottom:1px solid blue;margin-bottom:25px;text-align:center
}
.card-single{position:relative;width:100%;padding-bottom:20px;box-sizing:border-box;page-break-inside:avoid;break-inside:avoid
}
.card-single a{background:aqua;display:block;width:100%;height:100%;min-height:100px;border-radius:15px;overflow:hidden
}
.card-single img{display:block;width:100%;height:auto
}
.card-texts{text-align:center;position:absolute;border-radius:15px;top:0;width:100%;height:calc(100% - 20px)
}
.card-texts p{line-height:1em
}
.card-texts .card-short-content{position:absolute;top:48%;left:50%;width:80%;max-height:5em;overflow:hidden;transform:translateX(-50%) translateY(-50%)
}
.card-texts .card-stars{position:absolute;bottom:1px;left:50%;transform:translateX(-50%)
}
.card-texts .card-hashtags{position:absolute;left:50%;top:48%;width:80%;max-height:5em;overflow:hidden;transform:translateX(-50%) translateY(-50%)
}
.card-texts .card-hashtags,.card-texts .card-stars,.card-texts .card-short-content{-webkit-backface-visibility:hidden
}
.card-texts .card-replies{position:absolute;bottom:1px;right:15px
}
.card-texts .card-distance{position:absolute;bottom:1px;left:15px
}
.card-single a .card-hashtags,.card-single a .card-stars{color:#000;transition:all .3s
}
.card-single a .card-date,.card-single a .card-short-content,.card-single a .card-distance,.card-single a .card-replies{color:transparent;transition:all .3s
}
.card-single a:hover .card-texts{background-color:rgba(0,0,0,0.7)
}
.card-single a:hover .card-short-content,.card-single a:hover .card-date,.card-single a:hover .card-short-content,.card-single a:hover .card-distance,.card-single a:hover .card-replies,.card-single a:hover .card-stars{color:#dcdcdc
}
.card-single a:hover .card-hashtags{color:transparent
}
.hashtag-single::before{position:relative;top:-1px;content:'\f292';font-family:FontAwesome
}
.hashtag-single::after{content:' '
}
.card-distance::before{content:'\f041';font-family:FontAwesome
}
.card-stars::before{content:'\f005';font-family:FontAwesome
}
.card-replies::before{content:'\f27a';font-family:FontAwesome
}
.cardlist-enter-active,.cardlist-leave-active{transition:all 1s
}
.cardlist-enter,.cardlist-leave-active{opacity:0;transform:translateY(30px)
}



