
.readable-hidden{position:absolute;left:-9999px
}
.login-dim{position:fixed;z-index:100;width:100%;height:100vh;right:0;top:0;background-color:rgba(0,0,0,0.75);display:-ms-flexbox;display:flex;-ms-flex-pack:center;justify-content:center;-ms-flex-align:center;align-items:center
}
.login-dim.hidden{display:none
}
.login-dim .login{width:250px;height:200px;background:tomato
}
.login-dim .login .login_input>input{display:block;margin:0 auto
}
.app-menu{position:fixed;width:200px;height:100vh;background:tomato;left:-200px;top:0;transition:all .3s
}
.wrapper{background-color:gray;position:relative;left:0;transition:all .3s;overflow-x:hidden
}
.wrapper.menu-active{left:200px
}
.wrapper.menu-active .app-menu{position:fixed;left:0
}
.scroll-window{height:100vh;overflow-y:scroll;overflow-x:hidden
}
.scroll-window::-webkit-scrollbar{width:3px;height:0px
}
.parrot-header{position:relative;min-width:300px
}
.parrot-header .top-header{height:70px;display:-ms-flexbox;display:flex;-ms-flex-pack:center;justify-content:center;-ms-flex-align:center;align-items:center;position:relative;width:100%;max-width:850px;margin:0 auto
}
.parrot-header .top-header .open-menu-icon{background:none;border:none;position:absolute;left:0;margin-left:0px
}
.parrot-header .top-header .login-icon{position:absolute;right:0;margin-right:6px
}
.parrot-header .top-header .write-icon{position:absolute;right:0;margin-right:6px
}
.parrot-header .top-header i{font-size:30px;color:silver
}
.content-wrapper{width:100%;max-width:850px;min-width:300px;margin:0 auto;min-height:50vh
}



